.Project {
    display:flex;
    justify-content: center;
    background:#121212;
    height:100vh;
    color:#fff;
    font-family: 'Roboto Condensed', 'Lucida Sans Typewriter', 'Arial';

}

.Project-background {
    width:90%;
    height:96%;
}

.Project-heading h1 {
    font-weight: bolder;
}

.Project-table {
    border-collapse: collapse;
    table-layout: fixed;
    width: 100%;
}
th {
    font-weight: bold !important;
    font-size: 3vh;
}
td, th {
    border: 12px;
    text-align: left;
    padding: 8px;
    word-wrap: break-word;
  }
  tr:nth-child(even) {
    background-color: #0a2247;
  }

  @media only screen and (max-width: 600px) {
      .Project-table {
          font-size: 8px;
      }
.Project-heading h1 {
    font-size: 20px;
}
th {
    font-size: 1.3vh;
}
}