.Footer {
    width:100%;
    color: #fff;
    font-family: 'Roboto Condensed', 'Lucida Sans Typewriter', 'Arial';
    background: #212121;
    height:38vh;
   
}

.Footer .Footer-background-upper {
    display: flex;
    height:80%;
    align-items:center;

}
.Footer-content {
    position: relative;
    width:34%;
    text-align: center;

    
}

.Footer-content-two {
    position: relative;
    width:66%;
    text-align: center;

    
}
.Footer-background-lower {
    height: 20%;
    background: #3f3f3f;
    text-align: center;
    line-height: 8vh;
}

.Footer-content ul li {
    list-style: none;
    margin: 0 0 4vh 0;
    
}

.Footer-resume-button {
    width:50%;
    height:8vh;
    color:#fff;
    font-family: 'Roboto Condensed', 'Lucida Sans Typewriter', 'Arial';
    background: rgb(57, 95, 219);
    font-size: 3vh;
    border: none;
    box-shadow: 3px 5px 10px #0007;
    cursor: pointer;
    border-radius:2px;
}